@import url('https://fonts.googleapis.com/css2?family=Titillium+Web:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap');

.popup-desktop-overlay {
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  position: fixed;
  background: rgba(49,49,49,0.8);
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup-desktop-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 848px;
  max-width: 1000px; /* 448px * 2 */
  height: 529px;
}

.popup-desktop-img {
  width: 55%; /* 50% do container */
  height: 100%; /* Altura total do container */
  object-fit: fill;
}

.popup-desktop {
  flex: 1;
  max-width: none; /* 50% do container */
  height: 100%; /* Altura total do container */
  background: #fff;
  position: relative;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  z-index: 20;
  padding: 20px; 
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.popup-desktop-close-container {
  position: absolute;
  top: -25px;
  right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 30;
}

.popup-desktop-close {
  font-size: 16px;
  cursor: pointer;
  color: #fff;
  font-family: 'Lato', sans-serif;
  width: 42px;
  height: 21px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 40;
}

.popup-desktop-content {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.carta {
  margin-top: 40px;
  width: 28px;
  height: 29px;
}

h2 {
  font-size: 12px;
  font-family: 'Titillium Web', Arial, Helvetica, sans-serif;
  margin: 10px 0;
  text-transform: uppercase;
  color: #464B54;
  margin-top: 12px;
}

p {
  font-size: 16px;
  font-weight: 700;
  font-family: 'Titillium Web', Arial, Helvetica, sans-serif;
  margin: 10px 0 20px;
  color: #787D83;
}

input[type="email"] {
  width: calc(100% - 20px);
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  border-radius: 6px;

  &::placeholder {
    font-family: 'Titillium Web', Arial, Helvetica, sans-serif;
    font-size: 12px;
    color: #787D83;
  }
}

.popup-desktop-btn {
  background-color: #FAA500;
  color: #fff;
  text-transform: uppercase;
  padding: 10px 10px 10px 130px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  display: flex;
  align-items: center;
  font-size: 12px;
  font-weight: 700;
  font-family: 'Titillium Web', Arial, Helvetica, sans-serif;
  width: 258px;
  width: calc(100% - 20px);

  img {
    margin-left: 8px;
  }

  &:hover {
    background-color: #e69500;
  }
}

